.content {
	@extend .container;
	padding-top: var(--spacing-l);
	padding-bottom: var(--spacing-l);
	> * {
		max-width: var(--content-width);
		margin-right: auto;
		margin-left: auto;
	}

	p {
		margin-top: 0;
	}

	img {
		// @include mq($until: medium) {
		// 	max-width: 300px;
		// }
	}

	// h2,
	// h3,
	// h4,
	// h5 {
	// 	margin-top: var(--spacing-l);
	// 	margin-bottom: 0;
	// }

	a {
		color: var(--link-color);
		text-decoration: underline;
		text-decoration-color: var(--link-hover-color);
	}

	a:hover {
		color: var(--link-hover-color);
		text-decoration: underline;
	}

	blockquote {
		max-width: 700px;
		font-size: var(--font-size-h3);
		line-height: 1.4;
		margin: var(--spacing) auto var(--spacing-l) auto;
	}

	figure {
		max-width: none;
		margin: var(--spacing-xl) 0;
	}

	figcaption {
		padding: 1em 0;
		font-size: var(--font-size-xs);
		color: var(--color-text-dark);
		max-width: var(--content-width);
		margin-right: auto;
		margin-left: auto;
		border-bottom: 1px solid var(--color-grey-light);
	}

	ul {
		margin-top: 1.5em;
		padding: 0;
	}

	li {
		position: relative;
		padding-left: 1.2rem;
		margin-bottom: 0.3em;
	}

	li p {
		display: inline;
	}

	li:before {
		content: "•";
		position: absolute;
		left: 0;
		top: 0;
		font-size: 1.7rem;
		line-height: 1;
		font-weight: bold;
		margin-right: 0.6em;
		color: var(--color-text-dark);
	}

	.size-full {
		width: 100%;
		position: relative;
		@include mq(medium) {
			margin-top: var(--spacing-l);
			margin-bottom: var(--spacing-l);
			width: 120%;
			max-width: 120%;
			position: relative;
			left: -10%;
		}
	}
}
